<template>
  <div class="date-wrapper">
    <!--   -->
    <div class="month">
      <span class="btn" @click="weekPre"
        ><Icon type="arrow-left" :size="12" /> </span
      >
      <p class="date" v-if="false">{{ currentYear }}{{ currentMonth }}</p>
      <span class="btn-today" @click="today"></span>
      <span class="btn" @click="weekNext"
        > <Icon type="arrow-right" :size="12"
      /></span>
    </div>
    <!--  -->
    <ul class="weekdays">
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
    </ul>
    <!--  -->
    <ul class="days">
      <li
        :class="['day', currentClickDay === formatDate(day.getFullYear(), day.getMonth() + 1, day.getDate()) ? 'current' : '']"
        @click="pick(day)"
        v-for="(day, index) in days"
        :key="index"
      >
        <div class="week-no">{{ weekArr[index] }}</div>
        <!---->
        <span v-if="day.getMonth() + 1 != currentMonth" class="other-month">
          <span
            v-if="
              day.getFullYear() == new Date().getFullYear() &&
              day.getMonth() == new Date().getMonth() &&
              day.getDate() == new Date().getDate()
            "
            class="active"
            >{{ day.getDate() }}</span
          >
          <span v-else>{{ day.getDate() }}</span>
        </span>
        <div v-else class="day-no">
          <!---->
          <span
            v-if="
              day.getFullYear() == new Date().getFullYear() &&
              day.getMonth() == new Date().getMonth() &&
              day.getDate() == new Date().getDate()
            "
            class="active"
            >{{ day.getDate() }}</span
          >
          <span v-else>{{ day.getDate() }}</span>
        </div>
        <ul
          class="symbol-box"
          v-if="daysData[formatDate(day.getFullYear(), day.getMonth() + 1, day.getDate())]"
        >
          <li
            v-if="daysData[formatDate(day.getFullYear(), day.getMonth() + 1, day.getDate())].lunch"
            class="symbol lunch"
          >
            L
          </li>
          <li
            v-if="daysData[formatDate(day.getFullYear(), day.getMonth() + 1, day.getDate())].dinner"
            class="symbol dinner"
          >
            D
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>
<script>
import Icon from '@/components/Icon'
// eslint-disable-next-line no-extend-native
Date.prototype.format = function (fmt) {
  const o = {
    'M+': this.getMonth() + 1, // 
    'd+': this.getDate(), // 
    'h+': this.getHours(), // 
    'm+': this.getMinutes(), // 
    's+': this.getSeconds(), // 
    'q+': Math.floor((this.getMonth() + 3) / 3), // 
    S: this.getMilliseconds() // 
  }
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(
      RegExp.$1,
      (this.getFullYear() + '').substr(4 - RegExp.$1.length)
    )
  }
  for (let k in o) {
    if (new RegExp('(' + k + ')').test(fmt)) {
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length === 1 ? o[k] : ('00' + o[k]).substr(('' + o[k]).length)
      )
    }
  }
  return fmt
}
export default {
  name: 'date',
  props: {
    daysData: Object
  },
  components: {
    Icon
  },
  data () {
    return {
      currentYear: 1970, // 
      currentMonth: 1, // 
      currentDay: 1, // 
      currentWeek: 1, // 
      days: [],
      weekArr: ['', '', '', '', '', '', ''],
      currentClickDay: ''
    }
  },

  mounted () {
    this.$emit('updateDate', `${this.currentYear}${this.currentMonth}`, this.days)
  },

  created () {
    this.initData(null)
  },

  methods: {
    formatDate (year, month, day) {
      const y = year
      let m = month
      if (m < 10) m = `0${m}`
      let d = day
      if (d < 10) d = `0${d}`
      return `${y}-${m}-${d}`
    },

    initData (cur) {
      let date = ''
      if (cur) {
        date = new Date(cur)
      } else {
        date = new Date()
      }
      this.currentDay = date.getDate() //  
      this.currentYear = date.getFullYear() // 
      this.currentMonth = date.getMonth() + 1 // 
      this.currentWeek = date.getDay() // 1...6,0   // 
      if (this.currentWeek === 0) {
        this.currentWeek = 7
      }
      const str = this.formatDate(
        this.currentYear,
        this.currentMonth,
        this.currentDay
      ) //  --
      this.days.length = 0
      // 76  i<= 35- this.currentWeek
      /* eslint-disabled */
      for (let i = this.currentWeek - 1; i >= 0; i -= 1) {
        const d = new Date(str)
        d.setDate(d.getDate() - i)
        // console.log(y:" + d.getDate())
        this.days.push(d)
      }
      for (let i = 1; i <= 7 - this.currentWeek; i += 1) {
        const d = new Date(str)
        d.setDate(d.getDate() + i)
        this.days.push(d)
      }
    },

    //  
    weekPre () {
      const d = this.days[0] // 77
      d.setDate(d.getDate() - 7)
      this.initData(d)
      this.$emit('updateDate', `${this.currentYear}${this.currentMonth}`, this.days)
    },

    //  
    weekNext () {
      const d = this.days[6] // 77
      d.setDate(d.getDate() + 7)
      this.initData(d)
      this.$emit('updateDate', `${this.currentYear}${this.currentMonth}`, this.days)
    },

    //    
    pickPre (year, month) {
      const d = new Date(this.formatDate(year, month, 1))
      d.setDate(0)
      const newDate = this.formatDate(d.getFullYear(), d.getMonth() + 1, 1)
      this.initData(newDate)
    },

    //    
    pickNext (year, month) {
      const d = new Date(this.formatDate(year, month, 1))
      d.setDate(35)
      const newDate = this.formatDate(d.getFullYear(), d.getMonth() + 1, 1)
      this.initData(newDate)
    },
    today () {
      const today = new Date()
      this.initData(today.format('yyyy-MM-dd'))
      this.$emit('updateDate', `${this.currentYear}${this.currentMonth}`, this.days)
      this.$emit('pickDay', today.format('yyyy-MM-dd'))
      this.currentClickDay = ''
    },
    // 
    pick (date) {
      /*
      alert(
        this.formatDate(date.getFullYear(), date.getMonth() + 1, date.getDate())
      );
      */
      this.currentClickDay = date.format('yyyy-MM-dd')
      this.$emit('pickDay', date.format('yyyy-MM-dd'))
    }
  }
}
</script>
