<template>
  <div>
    <header class="header">
      <strong></strong><span>{{ displayDate }}</span>
    </header>
    <div class="calendar-box">
      <Calendar :daysData="myOrders" @pickDay="pickDay" @updateDate="updateDate" />
    </div>
    <div class="detail-box">
      <div class="card lunch">
        <div class="menu-box">
          <div class="title">
            <Icon type="food2" :size="24" color="#ffffff" />
            <h3></h3>
          </div>
          <div class="menu-box">
            <ul v-if="currentData.lunch.menus.length > 0" class="menu-list">
              <li :key="item" v-for="item in currentData.lunch.menus">
                {{ item }}
              </li>
            </ul>
            <div class="no-data" v-else></div>
          </div>
          <div v-if="currentData.lunch.canTake && currentData.lunch.menus.length > 0">
            <button v-if="myOrders[pickDate] && myOrders[pickDate].lunch" class="button btn-cancel" @click="cancelTake"> </button>
            <button v-else class="button" @click="take"> </button>
          </div>
          <div v-else class="button-msg">{{currentData.lunch.buttonMsg}}</div>
        </div>
      </div>
      <div class="line one-px"></div>
      <div class="card dinner">
        <div class="menu-box">
          <div class="title">
            <Icon type="food4" :size="24" color="#ffffff" />
            <h3></h3>
          </div>
          <ul v-if="currentData.dinner.menus.length > 0" class="menu-list">
            <li :key="item" v-for="item in currentData.dinner.menus">
              {{ item }}
            </li>
          </ul>
          <div class="no-data" v-else></div>
        </div>
        <div v-if="currentData.lunch.canTake && currentData.dinner.menus.length > 0">
          <button v-if="myOrders[pickDate] && myOrders[pickDate].dinner" class="button btn-cancel" @click="cancelTake"> </button>
          <button v-else class="button" @click="take"> </button>
        </div>
        <div v-else class="button-msg">{{currentData.dinner.buttonMsg}}</div>
      </div>
    </div>
    <Toast :show="showToast" :isSuccess="isSuccess" :msg="msg" @close="closeToast" />
  </div>
</template>
<script>
import Calendar from '@/components/Calendar'
import Icon from '@/components/Icon'
import Toast from '@/components/Toast'
import axios from 'axios'
import Dayjs from 'dayjs'
import { apiHost } from '@/utils/config'
import { pickMenus, pickMyOrders } from '@/utils/data'
export default {
  data () {
    return {
      showToast: false,
      msg: '',
      isSuccess: false,
      myOrders: {},
      pickDate: '',
      calendarArr: {
        type: 'combination',
        headStyle: {
          todayBtn: 'right',
          combination: 'center',
          checkBtn: 'right'
        },
        viewStyle: {
          day: 'right'
        },
        calendarData: []
      },
      displayDate: '',
      daysData: [],
      currentData: {
        lunch: {
          menus: [],
          take: true
        },
        dinner: {
          menus: [],
          take: false
        }
      }
    }
  },
  components: {
    Calendar,
    Icon,
    Toast
  },
  computed: {
  },
  watch: {
    displayDate: function (displayDate) {
      this.displayDate = displayDate
    }
  },
  mounted () {
    const today = Dayjs().format('YYYY-MM-DD')
    this.getMenus(today)
    this.getMyOrders()
  },
  methods: {
    closeToast () {
      this.showToast = false
    },
    getMyOrders () {
      const { start, end } = this.dateScpoe
      const userno = localStorage.getItem('auth')
      axios
        .post(`${apiHost}/dinner/a/wechat/api/lunchOrder/myOrderRecords`, {
          params: {
            userno,
            startSearchDay: start,
            endSearchDay: end
          }
        })
        .then(res => {
          this.myOrders = pickMyOrders(res.data.body.list)
        })
        .catch(err => {
          console.log(err)
        })
    },
    take (data) {
      const lunchId = this.currentData.lunch.id
      const loginName = localStorage.getItem('auth')
      axios
        .post(`${apiHost}/dinner/a/wechat/api/lunchOrder/order`, {
          lunchId,
          userno: loginName
        })
        .then(res => {
          this.showToast = true
          this.isSuccess = res.data.success
          this.msg = res.data.msg
          this.getMyOrders()
          console.log(res, 'success', this.showToast, this.isSuccess, this.msg)
        })
        .catch(err => {
          console.log(err)
        })
    },
    cancelTake (data) {
      const lunchId = this.currentData.lunch.id
      const loginName = localStorage.getItem('auth')
      axios
        .post(`${apiHost}/dinner/a/wechat/api/lunchOrder/cancelOrder`, {
          lunchId,
          userno: loginName
        })
        .then(res => {
          this.showToast = true
          this.isSuccess = res.data.success
          this.msg = res.data.msg
          this.getMyOrders()
        })
        .catch(err => {
          console.log(err)
        })
    },
    getMenus (date) {
      this.getLunch(date)
      this.getDinner(date)
    },
    getLunch (date) {
      axios
        .get(`${apiHost}/dinner/a/wechat/api/lunch/lunch`, {
          params: {
            startSearchDay: date,
            endSearchDay: date
          }
        })
        .then(res => {
          if (res.data.body.list[0]) {
            const { lunchContent, orderStart, orderEnd, id } = res.data.body.list[0]
            this.currentData.lunch = {
              menus: pickMenus(lunchContent),
              orderStart,
              orderEnd,
              id,
              canTake: Dayjs().isAfter(orderStart) && Dayjs().isBefore(orderEnd),
              buttonMsg: Dayjs().isAfter(orderEnd) ? '' : ''
            }
          } else {
            this.currentData.lunch = {
              menus: [],
              orderStart: '',
              orderEnd: ''
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    getDinner (date) {
      axios
        .get(`${apiHost}/dinner/a/wechat/api/lunch/dinner`, {
          params: {
            startSearchDay: date,
            endSearchDay: date
          }
        })
        .then(res => {
          if (res.data.body.list[0]) {
            const { lunchContent, orderStart, orderEnd, id } = res.data.body.list[0]
            this.currentData.dinner = {
              menus: pickMenus(lunchContent),
              orderStart,
              orderEnd,
              id,
              canTake: Dayjs().isAfter(orderStart) && Dayjs().isBefore(orderEnd),
              buttonMsg: Dayjs().isAfter(orderEnd) ? '' : ''
            }
          } else {
            this.currentData.dinner = {
              menus: [],
              orderStart: '',
              orderEnd: ''
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    pickDay (date) {
      this.pickDate = date
      this.getMenus(date)
    },
    updateDate (date, dates) {
      this.displayDate = date
      const [ start,,,,,, end ] = dates
      this.dateScpoe = {
        start: Dayjs(start).format('YYYY-MM-DD'),
        end: Dayjs(end).format('YYYY-MM-DD')
      }
    }
  }
}
</script>
